@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro&display=swap);
* {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

html {
  font-size: 18px;
  line-height: 1.45;
  display: flex;
  height: 100%;
  width: 100%;
  background-color: black;
}

body {
  margin: 0;
  font-family: "Source Sans Pro", -apple-system, BlinkMacSystemFont, "Segoe UI",
    "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  height: 100%;
  width: 100%;
}

#root {
  display: flex;
  height: 100%;
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --primary-color: rgb(77, 210, 255);
}

.App {
  width: 100%;
  height: 100%;
  max-width: 40rem;
  max-height: 70rem;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  background-color: white;
}

.App__text {
  flex: 1 1;
  overflow: hidden;
  position: relative;
}
.App__keyboard {
}

.Text {
  margin: 1rem;
  position: absolute;
  width: 100%;
  bottom: 0;
}

.Text--noLastBlock .Text__cursor {
  margin-left: 0.1em;
  padding-left: 2px;
  background-color: var(--primary-color);

  -webkit-animation: blink 900ms infinite;

          animation: blink 900ms infinite; /* IE 10+, Fx 29+ */
}

.Text--lastBlock .Text__lastChar {
  background-color: red;
  color: white;
}

.Text__line {
  margin: 0;
}

@-webkit-keyframes blink {
  0%,
  49% {
    opacity: 1;
  }
  50%,
  100% {
    opacity: 0;
  }
}

@keyframes blink {
  0%,
  49% {
    opacity: 1;
  }
  50%,
  100% {
    opacity: 0;
  }
}

.Keyboard {
}
.Keyboard__main {
  display: flex;
  flex-wrap: wrap;
}

.Button--block {
  letter-spacing: 0.2em;
}

.Button {
  border: 0;
  border-top: 2px solid black;
  border-left: 2px solid black;

  margin: 0;
  padding: 0;
  font: inherit;
  width: 33.3333%;
  height: 4rem;
  background-color: white;

  touch-action: none;
}

button.Button:focus {
  outline: 0;
}

.Button--startRow {
  border-left: none;
}

.Button--active {
  background-color: var(--primary-color);
}

.Button--negative.Button--active {
  background-color: black;
  color: white;
}

.Button--width-small {
  width: 16.66666%;
}

.Button--heigh-small {
  height: 2.5rem;
}

